@import './../../../SASS/mixins.scss';
@import './../../../SASS/variabls.scss';

.DailyWeatherT {
    background-color: var(#{--component-background-chosen-default});
    border-radius: 30px;
    border-color: var(#{--component-background-chosen-default});
    margin: 30px 8px 0 5px;
    height: 125px;
    width: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.DailyWeather {
    background-color: var(#{--component-background-switcher-day-default});
    border-radius: 30px;
    border-color: var(#{--component-background-switcher-day-default});
    margin: 30px 8px 0 5px;
    height: 125px;
    width: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.DWTemerature {
    margin-top: 5px;
    @include text (12px, Work Sans, var(#{--text-default}), 400);
}

.DWdayName {
    margin-top: 15px;
    @include text (12px, Work Sans, var(#{--text-default}), 400);
}

.DWdayImg {
    height: 50px;
}